.search-form {
    margin: 60px 0 30px 0;
    width: 100%;
}

.search-form__input {
    width: calc(100% - 80px);
    background: #F9F9F9;
    border-radius: 4px;
    min-height: 48px;
    border: none;
    padding-left: 10px;
    margin: 0;
    box-sizing: border-box;
}

.search-form__input::placeholder {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: var(--color-gray-span);
    padding: 15px 0 15px 22px;
}

.search-form__button {
    color: var(--color-white);
    background: var(--color-black);
    border-radius: 3px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    color: var(--color-white);
    width: 70px;
    min-height: 48px;
    margin: 0;
    margin-left: 10px;
    border: none;
    padding: 0;
}

.search-form__input:focus-visible {
    outline: var(--color-pink) auto 2px;
}
